<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-formgrid p-grid">
                <div class="p-field p-col-12">
                    <label for="other-drugs" class="p-mb-1">
                        Other Drugs
                    </label>
                    <p-input-text
                        v-model="form.otherDrugLine1"
                        id="other-drugs"
                        type="text"
                    />
                </div>
                <div class="p-field p-col-12">
                    <label for="other-drug-line2" class="p-mb-1">
                        Patient Comments
                    </label>
                    <p-input-text
                        v-model="form.patientComment"
                        id="other-drug-line2"
                        type="text"
                    />
                </div>
                <div class="p-field p-col-12">
                    <label for="other-drug-line2" class="p-mb-1">
                        Alert
                    </label>
                    <p-input-text
                        v-model="form.alertMsg"
                        id="other-drug-line2"
                        type="text"
                        style="color: red;"
                    />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import {
    computed,
    inject,
    reactive,
    ref,
    watch,
    onMounted,
    onUnmounted
} from "vue";
import { useStore } from "vuex";
import useVuelidate from "@vuelidate/core";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();

        const summaryForm = inject("summaryForm");
        const form = reactive({
            otherDrugLine1: null,
            otherDrugLine2: null,
            patientComment: null,
            alertMsg: null
        });

        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );

        watch(monitorDetails, (newValue) => {
            form.otherDrugLine1 = newValue.otherDrugLine1;
            form.patientComment = newValue.patientComment;
            form.alertMsg = newValue.alertMsg;
        });

        watch(form, (newValue) => {
            summaryForm.otherDrugLine1 = newValue.otherDrugLine1;
            // summaryForm.otherDrugLine2 = newValue.otherDrugLine2;
            summaryForm.patientComment = newValue.patientComment;
            summaryForm.alertMsg = newValue.alertMsg;
        });

        const rules = {};
        const v$ = useVuelidate(rules, form);
        const submitted = ref(false);
        const isValid = inject("isValid");
        const onSave = () => {
            submitted.value = true;
            isValid.otherDrugForm = true;
        };

        onMounted(() => {
            emitter.on("cycle-monitoring-toolbar:save", onSave);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return { form, v$, submitted };
    }
};
</script>
