<template>
    <p-card>
        <template #content>
            <div class="p-grid">
                <div class="p-col-12 p-md-6">
                    <label for="cycle-type" class="p-col-fixed p-mb-0">
                        <span class="cycle-info-label">Cycle Type:</span>
                        <span class="p-ml-2">
                            {{ cycle.type }}
                        </span>
                    </label>
                </div>
                <div class="p-col-12 p-md-6">
                    <label for="cycle-no" class="p-col-fixed p-mb-0">
                        <span class="cycle-info-label">Cycle #:</span>
                        <span class="p-ml-2">
                            {{ cycle.no }}
                        </span>
                    </label>
                </div>
                <div class="p-col-12">
                    <label for="cycle-start-date" class="p-col-fixed p-mb-0">
                        <span class="cycle-info-label">Cycle Start Date:</span>
                        <span class="p-ml-2 ">
                            {{ cycle.date }}
                        </span>
                    </label>
                </div>
                <div class="p-col-12 p-md-6">
                    <label for="cycle-cancelled" class="p-col p-mb-0">
                        <span class="cycle-info-label">Cycle Canceled:</span>
                        <span class="p-ml-2">
                            {{ cycle.cancelled ? "Yes" : "No" }}
                        </span>
                    </label>
                </div>
                <div class="p-col-12 p-md-6">
                    <label for="funded" class="p-col p-mb-0">
                        <span class="cycle-info-label">Funded : </span>
                        <span class="p-ml-2">
                            {{ selectedCycle.ohip ? "Yes" : "No" }}
                        </span>
                    </label>
                </div>
            </div>
        </template>
        <template #footer>
            <cycle-nav />
        </template>
    </p-card>
</template>

<script>
import {
    onMounted,
    reactive,
    ref,
    watch,
    computed,
    inject,
    onUnmounted
} from "vue";
import { useStore } from "vuex";
import { required } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";
import { emitter } from "@/event-bus";

import CycleNav from "@/components/shared/cycle-nav.vue";

export default {
    components: {
        CycleNav
    },
    setup() {
        const store = useStore();
        const selectedCycle = computed(
            () => store.state.artCycle.selectedCycle
        );

        const summaryForm = inject("summaryForm");
        const cycle = reactive({
            type: null,
            no: null,
            cancelled: false,
            funded: false,
            date: null
        });
        const rules = {
            type: { required },
            no: { required }
        };
        const v$ = useVuelidate(rules, cycle);
        const submitted = ref(false);
        const isValid = inject("isValid");
        const onSave = () => {
            submitted.value = true;
            isValid.cycleInfoForm = !v$.value.$invalid;
        };

        watch(cycle, (newValue) => {
            summaryForm.type = newValue.type;
            summaryForm.no = newValue.no;
            summaryForm.cancelled = newValue.cancelled;
            summaryForm.funded = newValue.funded;
            summaryForm.date = newValue.date;
        });

        const disable = ref(false);

        const fillCycleInfo = () => {
            let _cycle = store.state.artCycle.selectedCycle;
            if (_cycle) {
                cycle.type = _cycle.cycleType;
                cycle.no = _cycle.cycleNo;
                cycle.cancelled = _cycle.cancel;
                cycle.date = _cycle.cycleDate
                    ? _cycle.cycleDate.split(" ")[0]
                    : null;
                disable.value = true;
            }
        };

        watch(selectedCycle, (newValue) => {
            fillCycleInfo();
        });

        onMounted(() => {
            fillCycleInfo();
            emitter.on("cycle-monitoring-toolbar:save", onSave);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring-toolbar:save", onSave);
        });

        return {
            cycle,
            disable,
            submitted,
            v$,
            selectedCycle
        };
    }
};
</script>

<style scoped>
.cycle-info-label {
    color: #000000bf;
}
</style>
